
import React, {Component} from 'react';
import './Drumpad.css';

const data = [
    {id: ''},
    { id: 'snare', letter: 'Q', src: 'https://www.myinstants.com/media/sounds/snare.mp3' },
    { id: 'bass 1', letter: 'W', src: 'https://www.myinstants.com/media/sounds/bass-drum.mp3' },
    { id: 'bongo', letter: 'E', src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3' },
    { id: 'tom tom', letter: 'A', src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3' },
    { id: 'bass 3', letter: 'S', src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3' },
    { id: 'shotgun', letter: 'D', src: 'https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3' },
    { id: 'high hat', letter: 'Z', src: 'https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3' },
    { id: 'Side-Stick', letter: 'X', src: 'https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3' },
    { id: 'Snare', letter: 'C', src: 'https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3'  },
  ]





//console.log(typeof data[1].letter)





var name ='';



class Drumpad extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
        //markdown: initialState
    hitIndex: '',
    play: false,
    pause: true,
    color: ''
        };
  
   
        this.handleKeyPress=this.handleKeyPress.bind(this)

        this.handleKeyPress = this.handleKeyPress.bind(this)
    
     
        this.play = this.play.bind(this);
    
      };

 

      


componentDidMount(){
document.addEventListener("keydown", this.handleKeyPress)
};


componentWillMount(){
document.removeEventListener("keydown",this.handleKeyPress)
};





handleKeyPress(e) {
  if (e.keyCode === this.props.pad.keyCode) {
    const sound = document.getElementById(this.props.pad.letter);

    


    sound.play();

   
   
  };
  this.setState({
    color: "red"
  })


  if(e.keyCode === this.props.pad.keyCode){
    
    document.getElementById('xx').textContent= this.props.pad.id
   
   }



};
 
 

  play(e) {
    const sound = document.getElementById(this.props.pad.letter);

     if(e.target.id === this.props.pad.id){
  
       document.getElementById('xx').textContent= this.props.pad.id
     
     }








  sound.play();

this.setState({
hitIndex: e.target.getAttribute('id')

});


  

}



render(){





    return(


<div >
 <button    onClick={this.play} style = {this.props.mystyle} type="button" className="drum-pad" id={this.props.pad.id}>{this.props.pad.letter} 
        <audio className="clip" src={this.props.pad.src} id={this.props.pad.letter}  />

      </button>
      
  
 
    <p id="xx" >{name}</p>
      </div>

     

   

  


    );


}



}

export default Drumpad


